import React, { Component } from "react";
import Link from 'gatsby-link';
import {kebabCase} from 'lodash';
import FadeIn from './../components/UI/animations/FadeIn.jsx';
//import G1 from './../components/games/G1.jsx';
//let s = `${process.env.GATSBY_GAME_PATH + 'a.js'}`;
//import game from `${process.env.GATSBY_GAME_PATH + 'a.js'}`;

class Post extends Component {
  constructor(props){
    super(props);
    console.log("Post constructor");
    console.log(props);
  }

  render() {
    const { contentJson } = this.props.data; // data.markdownRemark holds our post data
    const { value, path, tags, game} = contentJson;
    console.log("json post");
    console.log(contentJson);
    return (
      <FadeIn>
      <div className="blog-post-container">
        <p>env: {process.env.GATSBY_GAME_PATH} path: {game}</p>
        json post {value} 
        <button onClick={()=>{
          //this.getGame();
        }}>request</button>
        <Link to={game}>Play Game {game}</Link>
        <h2>{game}</h2>
        {tags.map(t => {
            return <p key={t}>
                
                <Link to={"/tags/" + kebabCase(t)} key={t}>{t}</Link>
                </p>
        })}
      </div>
      </FadeIn>
    );
  }
}

export default function Template({
  data, // this prop will be injected by the GraphQL query below.
}) {
  return (<Post data={data} />)
}

export const pageQuery = graphql`
query MyContentQuery($path: String!){
    site{
        siteMetadata{
          title
        }
      },
    contentJson(path: {eq: $path}) {
        title,
        date,
        path,
        tags,
        game,
        content{
          type, text, options
        }
    }
}
`;

